@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;1,300;1,400&family=Plus+Jakarta+Sans:wght@300;400&family=Prata&family=Quattrocento&family=Quicksand:wght@300;400&display=swap');




/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* Base Styles */



:root {
        --clr-light-grey: #C0C9D0;
        --clr-cream: #E6DDDE;
        --clr-red: #F9585C;
        --fnt-title: 'Cormorant Garamond', serif;
        --fnt-p: 'Quicksand', sans-serif;

}



body {
    font-family: var(--fnt-p);
    width: 100%;
    background-color: var(--clr-cream);

}


a {
     text-decoration: none;
     transition: 500ms;

     &:visited {
         color: black;
     }


     &:hover{
         color: var(--clr-red)
     }
}
strong {
    font-size: 3rem;
}

span {
    color: var(--clr-red)

}


.porfolio__container-main {
   display: grid;
   grid-template-areas: 
   'header header header header'
   'main main main main'
   'footer footer footer footer';
    width: 100%;

}



/*header style */
header {
    display: flex;
    flex-flow: row nowrap;
    width: 50%;
    align-items: flex-end;
    position: fixed;
    top: 0;
    z-index: 10;
    margin-left: 50%;
    




}

/* Nav Style */

nav {


        width: 100%;
        justify-content:center;
        align-items: center;
        margin-right: 25px;
       
        

        .nav-links {
            background-color: var(--clr-cream);
            width: 60%;
            padding-top:10px;
            padding-bottom: 10px;
            display: flex;
            margin-left: 30%;
            justify-content: space-around;
            flex-flow: row nowrap;
            font-size: 1.1rem;
            border-radius: 0 0 .5rem .5rem;
            box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.174);


            a {
                padding: 5px;


                &:visited {
                    color: black;
                }
           
           
                &:hover{
                    color: var(--clr-red)
                }

            }
        }
}


/* Main Div Style */

main {
    display: flex;
    width: 100%;
    flex-flow: column nowrap;
}

/* Hero Box Style */


.hero {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    height: 100vh;
}

/* Image Box Styles */
.img-box {
    display: flex;
    width: 60%;


}

.big-img {
    width: 50%;
    height: 75vh;

    position: absolute;

    transition: 500ms;

    &:hover {
        transform: scale(1.1);
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;
    }
}

.medium-img {
    width: 35%;
    height: 35vh;
    z-index: 2;
    position: relative;
    margin-left: 525px;
    transform: translateX(85px);    

    transition: 500ms;

    &:hover {
        transform: scale(1.4) translateY(50px);
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;

    }

}

.small-img {
    width: 20%;
    height: 20vh;
    z-index: 5;
    position: relative;

    margin-top: 69vh;

    transition: 500ms;

    &:hover {
        transform: scale(2) translateX(60px);
        
        box-shadow: 0px 0px 2px 5px rgba(0, 0, 0, 0.071);
        z-index: 7;
    }

}


/* Hero Info Style */


.hero-info {
    display: flex;
    flex-flow: column nowrap;

    margin-top: 350px;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
}


.pretitle {
    font-size: 1.5rem;
    position: relative;
    padding-bottom: 50px;
}

h1 {
    font-size: 10rem;
    font-family: var(--fnt-title);
    position: absolute;
    z-index: 2;

align-self: center;
margin-left: 82vh;
margin-top: 15vh;

    

}


.subtitle {
    width: 48%;
    text-align: left;
    font-size: 1.3rem;
    padding-top: 200px;
}


/*Hero Info Button */@at-root





 a.btn-contact {
    text-transform: uppercase;
    padding: 20px;
    margin-top: 50px;
    margin-left: 300px;
    text-align: center;
    transition: 500ms;
    font-size: 3rem;

    
}


/* about section style */@at-root

.about {
    width: 100%;
    display: flex;
    flex-flow: row nowrap; 

    img {
        transition: 500ms;
    &:hover {
        box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.079);
    }
}
}

.about-img-box {
    width: 33%;
    height: 100vh;


    

    
}


.about-img1 {


        margin-top: 10vh;
        width: 65vh;
        height: 75vh;
        aspect-ratio: 16 / 9;
        margin-left: 25px;
}

.about-text-box {
    width: 33%;
    height: 65%;
    margin-left: 50px;
    margin-right: 50px;

    padding: 100px;
    margin-top: 25vh;
    padding-bottom: 0px;


    transition: 500ms;

    h2 {
        font-family: var(--fnt-title);
        font-size: 4rem;
        margin-bottom: 100px;


    }
    


    
}
 
.about-second-img-box {
    width: 40%;


    .about-img2 {

            margin-bottom: 10px;
            width: 40vh;
            height: 35vh;
            aspect-ratio: 16 / 9;
    }
    
    .about-img3 {

        margin-top: 100px;
        margin-left: 100px;
        width: 43vh;
        height: 55vh;
        aspect-ratio: 16 / 9;
}



}


/* Experience */

.experience {
    width: 100%;
} 

.experience__container {
    width: 100%;

}

h3 {
    margin-top: 10vh;
    font-size: 4rem;
    font-family: var(--fnt-title);
}

.experience__list {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    margin-top: 100px;
    padding-bottom: 100px;

    
    a{
        
        font-size: 5rem;
        border-bottom: 4px solid var(--clr-red);
        cursor: pointer;
        margin-bottom: 20px;
        padding-left: 50px;


        
    }

}

.selected {
    color: var(--clr-red)
}


.exp {
    display: none;
    flex-flow: column nowrap;
    border-bottom: 4px solid var(--clr-red);
    transition: 500ms;
    width: 100%;

}

.exp-display {
    display: flex;

    
}


.exp-info {
    display: flex;
    border: none;
    width: 90%;
    align-self: center;
    padding: 20px;
    align-items: center;


}

.exp-item {
    text-align: left;
}


.exp-data {

    width:  50%;
    padding-left: 35px;
}
.exp-date {
    font-size: 1.3rem;
    font-weight: bold;

}

.exp-position {
    font-size: 1.4rem;
    font-weight: bold;
}


/*Portfolio Section */


.portfolio {
    width: 100%;
    display: flex;
    height: 100vh;
}




/* Footer */@at-root

footer {
    grid-area: footer;
    width: 100%;

    
}

.footer-container {
    display: flex;
    width: 100%;

}





